.container_en_ce_moment h3{
  margin-top:3px;
  font-size: 1.2vw;
  padding-bottom: 5vh;
}

.container_en_ce_moment{
  background-color: #393939;
  border-radius: 20px;  
  padding: 0 12vh;
  margin-bottom:30vh;
}



.button_center_absolute{
  transform: translateY(50%); 
  margin: 20px auto 0 auto;
}

.film{
  display: flex;
  padding: 0;
  flex-direction: column;
  align-items: center; 
  border-radius: 20px; 
}

.container_en_ce_moment h3{
  color: #f6f7eb;
  padding-top: 10px;
}

.film-row {
  padding-top:8vh;
}

.film img:hover{
  border: 1px solid #77EECC;
  border-radius: 10px;
}
.film_ECM:hover {
  
}


@media screen and (max-width: 480px){

  .en_ce_moment {
    display: flex;
    flex-direction: column;
  }

  .en_ce_moment h2{
    display: flex;
    flex-direction: column;
    font-size:2rem;
  }

  .en_ce_moment h3{
    font-size:1.4rem;
  }
  .container_en_ce_moment{
    padding:0 3vh;
  }

  .film-row{
    padding:0;
  }

  .film-row-top{
    padding-top: 5vh;
  }

} 